// 
// _theme-mode.scss
// 


/* Root Prefix Variable */
:root {
    --#{$prefix}logo-lg-height:                21px;
    --#{$prefix}logo-sm-height:                24px;

    --#{$prefix}main-nav-width:               250px;
    --#{$prefix}main-nav-width-sm:             80px; 
    --#{$prefix}main-menu-item-icon-size:       20px;
    --#{$prefix}main-menu-item-font-size:       14px;
    --#{$prefix}main-menu-item-padding-x:       15px;
    --#{$prefix}main-menu-item-padding-y:       10px;
    --#{$prefix}main-menu-item-margin-y:       2px;

    --#{$prefix}topbar-height:                 70px;
    --#{$prefix}footer-height:                 60px;
    
    // Heading Variable
    --#{$prefix}headings-color:                 #313b5e;

    --#{$prefix}input-border-color:             #d8dfe7;
    --#{$prefix}input-focus-border-color:       #b0b0bb;
}


/* Color system - Dark Mode only */
html[data-bs-theme="dark"],
[data-bs-theme="dark"] {

    --#{$prefix}border-color:                   #2f3944;

    --#{$prefix}light:                           #{$body-tertiary-bg-dark};
    --#{$prefix}dark:                            #{$body-tertiary-color-dark};
    
    --#{$prefix}light-rgb:                       #{to-rgb($body-tertiary-bg-dark)};
    --#{$prefix}dark-rgb:                        #{to-rgb($body-tertiary-color-dark)};

    // Heading Variable
    --#{$prefix}headings-color:                  #{$body-color-dark};

    --#{$prefix}input-border-color:             #3a4551;
    --#{$prefix}input-focus-border-color:       #4a5663;
}

/* Main Menu Light */
html.sidebar-light{
    --#{$prefix}main-nav-bg:                               #ffffff;
    --#{$prefix}main-menu-item-color:                       #5a5b70;
    --#{$prefix}main-menu-item-hover-bg:                    #f3f1fa;
    --#{$prefix}main-menu-item-hover-color:                  #{$primary};
    --#{$prefix}main-nav-border-color:                     #eaedf1;
}


/* Main Menu Dark */
html.sidebar-dark {
    --#{$prefix}main-nav-bg:                               #26303a;
    --#{$prefix}main-menu-item-color:                       #a0aabf;
    --#{$prefix}main-menu-item-hover-bg:                    #283643;
    --#{$prefix}main-menu-item-hover-color:                 #{$white};
    --#{$prefix}main-nav-border-color:                     #2f3944;
}


/* Main Menu Dark (Dark Mode Only) */
html[data-bs-theme="dark"].sidebar-dark,
html[data-bs-theme="dark"].sidebar-light {
    --#{$prefix}main-nav-bg:                               #282f36;
    --#{$prefix}main-menu-item-color:                       #afb9cf;
    --#{$prefix}main-menu-item-hover-bg:                    #303943;
    --#{$prefix}main-menu-item-hover-color:                 #{$white};
    --#{$prefix}main-nav-border-color:                     #2f3944;
}


/* Topbar Light */
html.topbar-light {
    --#{$prefix}topbar-bg:                                 #{$white};
    --#{$prefix}topbar-item-color:                         #707793;
    --#{$prefix}topbar-search-bg:                          #f3f6f9;
}


/* Topbar Dark */
html.topbar-dark {
    --#{$prefix}topbar-bg:                                 #22b956;
    --#{$prefix}topbar-item-color:                         #e9ebf0;
    --#{$prefix}topbar-search-bg:                          transparent;
}


/* Topbar Dark (Dark Mode Only) */
html[data-bs-theme="dark"].topbar-dark, 
html[data-bs-theme="dark"].topbar-light {
    --#{$prefix}topbar-bg:                                 #282f36;
    --#{$prefix}topbar-item-color:                         #afb9cf;
    --#{$prefix}topbar-search-bg:                          #303943;
}